

#note-display{
  font-family: 'Courier New', 'Courier New', Courier, monospace;
  text-align: left;
  white-space: pre;
  width: 65vw;
  overflow-x: auto;
}

@media (max-width: 1065px) {
  #note-display{
      font-size: 16px; /* Constant value */
  }
}

/* Font size for screens 600px and above */
@media (min-width: 1066px) {
  #note-display {
      font-size: 1.5vw; /* Percentage of viewport width */
  }
}

.guitar-editor{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    height: 90vh;
    width: 90vw;
}

.guitarController{
    width: 80vw;
    height: auto;
}

#top-content{
    display: flex;
    width: 80vw;
    height: 40vh;
    justify-content: space-between;
    align-items: center;
}

#save-button{
    display: flex;
    justify-content: center;
    align-items: center;
}

/* CSS */
.button-53 {
  background-color: #3DD1E7;
  border: 0 solid #E5E7EB;
  box-sizing: border-box;
  color: #000000;
  display: flex;
  font-family: ui-sans-serif,system-ui,-apple-system,system-ui,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
  font-size: 1rem;
  font-weight: 700;
  justify-content: center;
  line-height: 1.75rem;
  padding: .75rem 1.65rem;
  position: relative;
  text-align: center;
  text-decoration: none #000000 solid;
  text-decoration-thickness: auto;
  width: 100%;
  max-width: 460px;
  position: relative;
  cursor: pointer;
  transform: rotate(-2deg);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.button-53:focus {
  outline: 0;
}

.button-53:after {
  content: '';
  position: absolute;
  border: 1px solid #000000;
  bottom: 4px;
  left: 4px;
  width: calc(100% - 1px);
  height: calc(100% - 1px);
}

.button-53:hover:after {
  bottom: 2px;
  left: 2px;
}

@media (min-width: 768px) {
  .button-53 {
    padding: .75rem 3rem;
    font-size: 1.25rem;
  }
}

#submit-form{
    height: 30vh;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width: 10vw;
}

#title-enter{
  width: 10vw;

